<template lang="html">
	<div class="container">
		<article class="content">
			<div class="blue-strip"></div>
			<span class="type" v-text="describe"></span>
			<h2 v-text="title"></h2>
			<section :class="['section-content', {center: isCenter}]" v-html="data"></section>

			<footer class="copy-right">
				Copyright © 2016 老外 · 关于本站 · Powered by GitHub · Design by 老外
			</footer>
			<div class="blue-one"></div>
			<div class="blue-two"></div>
		</article>
</template>

<script>
export default {
	data () {
		return {
			isCenter: false
		}
	},
	computed: {
		describe() {
			return this.$store.state.finalData.type
		},
		title() {
			return this.$store.state.finalData.title
		},
		data() {
			return this.$store.state.finalData.content
		},
		isCenter() {
			return this.$store.state.finalData.isCenter
		}
	}
}
</script>

<style lang="css">
.copy-right {
	font-size: 12px;
}
.section-content P {
	text-indent: 2em;
}
.section-content blockquote {
	margin: 0;
	padding: 16px;
	background: rgba(234, 234, 234, 1)
}
.section-content pre {
	overflow:auto;
	padding: 20px;
	background: rgba(234, 234, 234, 1);
	border-radius: 7px;
}
.section-content pre span {
	color: rgba(5, 147, 211, 1)
}
.center {
	text-align: left;
}
footer {
	padding: 25px 0 25px
}
</style>



