import * as v from './mutation-types.js'

export default {
	[v.GET_DATA] (state, title) {
		if(title === state.future.title){
			state.finalData = state.future
		}else if(title === state.jsPrototype.title) {
			state.finalData = state.jsPrototype
		}else if (title === state.es6.title) {
			state.finalData = state.es6
		}else {
			state.finalData = ''
		}
	}
}



