import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations.js'
import actions from './actions.js'

Vue.use(Vuex);

const state = {
	finalData:'',
	future: { type:'#奇说怪谈', isCenter:false, title:'Future', content: '<p>This is my name，</p><p>I write a legend，however，</p><p>If I want，</p><p>No one can stop me，</p><p>And all from the darkness，</p><p>So I go，</p><p>Live or die，</p><p>let me choose.</p>'},
	es6:{ type:'#ES6', isCenter:true, title:'ECMAScript 6之艺术篇', content:`<h3>Map与Set</h3>
	<p>Map和Set都可以把类似数组结构的，通过扩展运算符转成数组，但set去掉重复的成员。代码如下：</p>
	<pre lang='undefined'>
	<span>const</span> set = <span>new</span> Set([1, 2, 3, 3]);
	[...set]   // [1, 2, ,3]
	set.<span>has</span>(1)  // true
	set.<span>add</span>(0) // [1, 2, 3, 0]
	set.<span>delete</span>(0) // true  表示是否删除成功
	set.<span>clear</span>() // 清除所有成员，无返回值

	<span>let</span> week = <span>new</span> Map([
				[ 0, &#39;星期日&#39; ],
				[ 6, &#39;星期六&#39; ]
			]);
	[...week]  // [[0, &#39;星期日&#39;], [6, &#39;星期六&#39;]]
	</pre>
	<p>Map和Set同样具有keys()、values()、entries()、forEach()三个遍历方法，以及size，clear()方法，不同的是Map的has(key)、delete(key)，而Set是针对value，has(value)、delete(value)。除此之外Map具有get(key)和set(key, value)，而Set是add(value)，它不具有get方法。代码如下：</p>
	<pre lang='undefined'>
	<span>let</span> week = <span>new</span> Map([
			[ 0, &#39;星期日&#39; ],
			[ 6, &#39;星期六&#39; ],
			[ 5, &#39;星期五&#39; ],
			[ 4, &#39;星期四&#39; ],
			[ 3, &#39;星期三&#39; ],
			[ 2, &#39;星期二&#39; ],
			[ 1, &#39;星期一&#39; ]
	]);
	<span>let</span> day = date.<span>getDay</span>();
	<span>for</span> (let [key, value] of week.<span>entries</span>()){
		<span>if</span>(key === day){
			day = value
		}
	}
	week.<span>size</span>   //  7
	week.<span>get</span>(0) // &#39;星期日&#39;
	</pre>
	<h3>Promise与Async</h3>
	<p><span>Promise</span>通俗来讲就是一个容器，里面通常是一个异步操作。它具有三种状态，<span>Pending</span>(进行中)、<span>Resolved</span>(已完成)、<span>Rejected</span>(已失败)。promise执行后只会在这三种状态中从Pending变为Resolved和从Pending变为Rejected。换句话说就像收快递一样，一旦发货无法取消，快递在发送时永远是发送中，直到快递到达某一站，更新下状态。这也算是promise的缺陷。</p>
	<pre lang='undefined'>
	<span>const</span> loadImg = <span>new</span> Promise((resolve, reject) => {
	   $.<span>ajax</span>({
	     url:&#39;....&#39;,
	     type:&#39;GET&#39;,
	     dataType:&#39;JSON&#39;,
	     success:(data)=>{
	        <span>resolve</span>(data);
	     }
	   })
	})

	loadImg.then((value)=>{console.log(value)})
	</pre>
	<p><span>then</span>方法相当于返回一个新的<span>Promise</span>实例，可以采用链式写法。<span>then</span>方法可以接受两个回调函数作为参数，分别是状态为Reject和Resolved时调用。resolve(new Promise())参数可以是另一个promise实例。</p>
	<h3>Promise.all()与Promise.race()</h3>
	<p>1、只有数组里的所有promise的状态都变成<span>resolved</span>，p的状态才会变成<span>resolved</span>，之后返回值会组成一个数组，再传递给p的回调函数；</p>
	<p>2、只要数组里其中有个promise被<span>rejected</span>，p的状态就会变成<span>rejected</span>，之后传递给p的回调函数</p>
	<pre lang='undefined'>
	<span>const</span> p = Promise.all([p1, p2, p3])

	//<span>Promise.all</span>接受一个数组作为参数，数组里的每个成员都是promise实例

	<span>const</span> p = Promise.race([p1, p2, p3])
	//只要数组成员中的一个实例率先改变状态，p的状态就会改变，率先改变的实例返回值将传递给p的回调函数。
	</pre>
	<h3>Async函数</h3>
	<p>将异步操作以同步的写法实现，避免了多层嵌套，使得异步操作更加方便，因为嵌套不但导致代码难读懂，而且维护起来比较绕，不直观。异步指得是一个任务分成两个过程，先执行第一个过程，然后可以去执行其他任务，等第一个过程准备完毕，再去执行第二个过程。</p>
	<pre lang='undefined'>
	<span>const</span> taskOne = <span>new</span> Promise((resolve, reject) => {
	  console.log(&#39;first task&#39;)
	  <span>resolve</span>();
	});
	<span>const</span> taskTwo = <span>new</span> Promise((resolve, reject) => {
	  console.log(&#39;second task&#39;)
	  <span>resolve</span>();
	});
	<span>const</span> timeout = <span>async</span> () => {
	  <span>await</span> taskOne;
	  <span>await</span> taskTwo;
	}
	timeout().<span>then</span>(() =>{
	  console.log(&#39;well done&#39;)
	});
	</pre>
	<p>这个函数优雅快捷的取代了<span>Generator</span>函数以及<span>co</span>模块的结合写法。因为Generator函数是一个遍历器对象，自身不会自动执行，需要用到遍历器对象next方法，使得指针移向下一状态，通过yield暂停执行。而有了co模块就使得<span>Generator</span>能够自动执行。</p>
	<h3>Class与extends</h3>
	<p><span>Class</span>是ES6优雅的语法糖。更加像面向对象编程。</p>
	<pre lang='undefined'>
	<span>class</span> Person {
	  <span>constructor</span>(){
	    // ...
	  }
	  sayHi(){

	  }
	}

	// 等同于
	<span>function</span> Person(){}
	Person.prototype = {
	  sayHi(){}
	};
	</pre>
	<p>所以方法是创建在原型上的，每个实例内部都不具有原型上的方法，因此方法不会被重复创建，与我另一篇《JavaScript原型篇》讲到的一样。</p>
	<p>接下来我们看下extends</p>
	<pre lang='undefined'>
	<span>class</span> Person { }

	<span>class</span> Tony <span>extends</span> Person {
	  <span>sconstructor</span>() {
	  	super()
	  }
	}

	<span>let</span> man = <span>new</span> Tony();

	Tony.__proto__ === Person // true
	Tony.prototype.__proto__ === Person.prototype // true
	</pre>
	<p>子类必须在构造函数里调用super()方法，否则会报错。子类是没有this对象的，而是继承父类的this对象。</p>` },

	jsPrototype:{ type:'#JavaScript', title:'JavaScript之原型篇', isCenter:'true', content:`<p>本文讲述通过构造函数、原型对象、原型链以及原型继承创建自定义类型的方法。</p>
	<h3><strong>构造函数</strong></h3>
	<p>​      函数还具备一种叫构造函数的使用。每个函数都会默认创建constructor，当然我们也可以自己去创建自定义的构造函数。如下代码所示：</p>
	<pre>
	<span>function</span> Person(name, age){
	    <span>this</span>.name = name;
	    <span>this</span>.age = age;
	    <span>this</span>.sayName = <span>function</span>(){
	        console.log(<span>this</span>.name);
	    };
	}

	<span>var</span> personOne = <span>new</span> Person(&quot;Tom&quot;, 16);
	<span>var</span> personTwo = <span>new</span> Person(&quot;Jack&quot;, 17);
	personOne.sayName();  // Tom
	personTwo.sayName();  // Jack
	</pre>
	<p>构造函数区别于其他函数的一点是在于他们的调用方式不同，需要new 一个对象来调用。于是我们可以观察到，每new一个person对象，都会在对象里创建于Person具有一样的属性和方法，如果创建多个对象，会重复创建sayName()方法。这样明显不好，于是想把sayName()这个方法抽离出来，能够使它只创建一次，但是抽出来后，发现这个sayName()方法是全局的，任何都可以调用到改方法，就没有封装思想可言。接下来我们来看下原型。</p>
	<h3><strong>Prototype</strong></h3>
	<p>每个函数创建后都有一个prototype属性。它可以实现所有对象实例共享它所包含的属性和方法，因此它就可以解决让每个实例都能调用到方法，且不会被重复创建。</p>
	<pre>
	<span>function</span> Person(){}
	Person.prototype.name = &quot;Tony&quot;;
	Person.prototype.age = 16;
	Person.prototype.sayName = <span>function</span>(){
	  console.log(<span>this</span>.name);
	};

	<span>var</span> personOne = <span>new</span> Person();
	<span>var</span> personTwo = <span>new</span> Person();

	personOne.name = &quot;Tom&quot;;
	personOne.name;     // &quot;Tom&quot;，源于实例自身的name
	personTwo.name;     // &quot;Tony&quot;，源于原型的name
	</pre>
	<p>new出来的每个实例都会有个内部指针proto，指向Person.prototype。这个指针会先在实例内部检索属性和方法，如果没有检索到会往原型上继续检索，再没找到会继续往上找。从上面的代码，可以看出personOne修改name为Tom，而personTwo.name得到是Tony，因此personOne.name=&quot;Tom&quot;并没能修改到原型上的name。而是修改personOne自己的内部的name，咦，这样哪里有实例的属性和方法共享？对，我们现在只实现了方法不被重复创建。</p>
	<h3>构造函数与原型结合运用</h3>
	<pre>
	<span>function</span> Person(name, age){
	    <span>this</span>.name = name;
	    <span>this</span>.age = age;
	    <span>this</span>.skill = [&quot;run&quot;, &quot;say&quot;];
	}

	Person.prototype = {
	    sayName : <span>function</span>(){
	        console.log(<span>this</span>.name);
	    }
	}

	<span>var</span> personOne = <span>new</span> Person(&quot;Tom&quot;, 17);
	<span>var</span> personTwo = <span>new</span> Person(&quot;Tony&quot;, 16,);
	personOne.skill.push(&quot;singing&quot;);
	personOne.skill // [&quot;run&quot;, &quot;say&quot;, &quot;singing&quot;]
	personTwo.skill // [&quot;run&quot;, &quot;say&quot;]
	personOne.sayName === personTwo.sayName  // true
	</pre>
	<p>这样一来，就可以实现每new一个对象都会具备构造函数的属性和方法，同时new出来的每个对象也都共享到原型上的属性和方法。</p>
	<h3>原型链</h3>
	<p>我们回顾下上面的代码，可以发现原型和实例与构造函数之间的关系链，每个构造函数都会有一个原型对象，原型对象有一个指向构造函数的指针，而通过构造函数实例出来的对象也有个内部指针，指向构造函数的原型对象。</p>
	<h3>原型继承</h3>
	<p>在ES6前，没有class和extends关键字去描述类与继承的关系，而是通过原型链来模拟继承，其实本质更像是重写原型对象的方法。如下代码所示：</p>
	<pre>
	<span>function</span> Animal(){
	    <span>this</span>.animalName = &#39;animal&#39;;
	}
	Animal.prototype.getName = <span>function</span>(){
	    return <span>this</span>.animalName;
	};

	<span>function</span> Dog(){
	    <span>this</span>.dogName = &#39;dog&#39;;
	}

	// 继承了 Animal
	Dog.prototype = <span>new</span> Animal();

	Dog.prototype.getNameTwo = <span>function</span> (){
	    return <span>this</span>.dogName;
	};

	<span>var</span> dog = <span>new</span> Dog();
	dog.getName() // animal
	dog.getNameTwo() // dog
	</pre>` }
}

export default new Vuex.Store({
	state,
	mutations,
	actions
})



